import React,{Fragment} from 'react'
import './index.css'
import {NavLink} from 'react-router-dom'
import PubSub from 'pubsub-js'
export default function Nav() {
    let [navHeight,setNavHeight]=React.useState('0')
    const handleNavHeight=(title,data)=>{
        setNavHeight(data)
    }
    PubSub.subscribe('navHeight',handleNavHeight)
    return (
       <Fragment>
        <div className='nav-pc'>
        <ul>
            <li> <NavLink to='/home' activeClassName='navActive'><span className="icon-gaishu iconfont nav-pc-icon"></span>首页</NavLink></li>
            <li> <NavLink to='/essay' activeClassName='navActive'><span className="icon-tubiaozhizuomoban- iconfont nav-pc-icon"></span>随笔心得</NavLink></li>
            <li> <NavLink to='/tecnology' activeClassName='navActive'><span className="icon-gouwuche iconfont nav-pc-icon"></span>技术分享</NavLink></li>
            <li> <NavLink to='/tool' activeClassName='navActive'><span className="icon-gerenzhongxin iconfont nav-pc-icon"></span>功能栏</NavLink></li>
            <li> <NavLink to='/message' activeClassName='navActive'><span className="icon-guanyuwomen iconfont nav-pc-icon"></span>留言板</NavLink></li>
        </ul>
    </div>
    <div className='nav-pe' style={{height:navHeight}}>
        <ul>
            <li> <NavLink to='/home' activeClassName='navActive'><span className="icon-gaishu iconfont nav-pe-icon"></span> 首页</NavLink></li>
            <li> <NavLink to='/essay' activeClassName='navActive'><span className="icon-tubiaozhizuomoban- iconfont nav-pe-icon"></span> 随笔心得</NavLink></li>
            <li> <NavLink to='/tecnology' activeClassName='navActive'><span className="icon-gouwuche iconfont nav-pe-icon"></span> 技术分享</NavLink></li>
            <li> <NavLink to='/tool' activeClassName='navActive'><span className="icon-gerenzhongxin iconfont nav-pe-icon"></span> 功能栏</NavLink></li>
            <li> <NavLink to='/message' activeClassName='navActive'><span className="icon-guanyuwomen iconfont nav-pe-icon"></span> 留言板</NavLink></li>
        </ul>
    </div>
    </Fragment>
    )
}
